.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 80px;
  margin: 80px 170px;
}

.project-title {
  position: relative;
}

.project-title h1 {
  padding: 0px 30px;
  font-size: 80px;
  font-weight: 600;
}

.project-title img {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.projects-format {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding: 60px;
  border-radius: 10px;
  border: 2px solid white;
  transition: 0.4s;
  cursor: pointer;
}

.project-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  margin-bottom: 80px;
}

.projects-format h3 {
  font-size: 28px;
  font-weight: 600;
}

.projects-format h2 {
  font-size: 38px;
  font-weight: 800;
  background: linear-gradient(270deg, #df8908 50%, #b415ff 100.6%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.projects-format p {
  color: #d4d4d4;
  font-size: 24px;
  line-height: 44px;
  /* max-width: 300px; */
  white-space: pre-line;
}

.projects-code {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-top: 20px;
}

.projects-format:hover {
  border: 2px solid #ff00ff;
  background-image: linear-gradient(45deg, #3f0028, #582300);
  transform: scale(1.05);
  transition: 0.4s;
}

.anchor-link {
  text-decoration: none;
  color: white;
}
